import { Link } from "react-router-dom"
import { COLLAPSED_SIDEBAR_WIDTH, SIDEBAR_WIDTH } from "@rp/common/settings"
import { RPThemeProps, styled } from "@rp/react/theme"
import { Avatar, Button, Drawer, Menu, SxProps, alpha } from "@rp/react/web/mui/core"

const baseButtonStyles = (
  theme: RPThemeProps,
  selected?: boolean,
  variant: "hover" | "click" = "hover",
) => ({
  display: "flex",
  justifyContent: "left",
  gap: theme.spacing(1),
  alignItems: "center",
  textDecoration: "none",
  width: "100%",
  borderRadius: theme.spacing(0.5),
  transition: "background-color 0.2s ease-in-out",
  cursor: "pointer",
  paddingLeft: theme.spacing(1),
  paddingRight: theme.spacing(1),
  paddingTop: theme.spacing(0.75),
  paddingBottom: theme.spacing(0.75),
  "&:hover": {
    backgroundColor: theme.palette.overlay.surface,
  },
  "&:hover p, &:hover svg": {
    color: variant === "click" ? undefined : theme.palette.text.primary,
  },
  "& p": {
    textTransform: variant === "click" ? undefined : "none",
    fontSize: variant === "click" ? 12 : 13,
    lineHeight: "24px",
    color: selected ? theme.palette.text.primary : theme.palette.grey[300],
    transition: "color 0.2s ease-in-out",
    fontWeight: selected || variant === "click" ? 700 : undefined,
  },
  "& svg": {
    width: 24,
    height: 24,
    color: selected ? theme.palette.text.primary : theme.palette.grey[300],
    transition: "color 0.2s ease-in-out",
  },
})

const StyledSideBarLinkContained = styled(Link)(({ theme }) => ({
  ...baseButtonStyles(theme, false, "click"),
  borderRadius: "35px",
  textTransform: "none",
  backgroundColor: alpha(theme.palette.primary.main, 0.2),
  fontSize: 16,
  fontWeight: 500,
  color: theme.palette.primary.main,
  display: "flex",
  flexDirection: "row",
  justifyContent: "center",
  "& svg, &:visited, &:visited svg": {
    color: theme.palette.primary.main,
    fill: theme.palette.primary.main,
  },
  "&:hover": {
    backgroundColor: alpha(theme.palette.primary.main, 0.3),
  },
}))

const StyledSideBarMenu = styled(Button, {
  shouldForwardProp: prop => prop !== "selected" && prop !== "menuOpen" && prop !== "menuVariant",
})<{
  selected?: boolean
  menuOpen?: boolean
  menuVariant: "button" | "text"
}>(({ theme, selected, menuOpen, menuVariant }) => {
  const baseBgColor = alpha(theme.palette.primary.main, 0.2)
  const baseColor = theme.palette.text.primary

  if (menuVariant === "text") {
    return {
      ...baseButtonStyles(theme, selected, "click"),
      textTransform: "unset",
      backgroundColor: menuOpen ? theme.palette.overlay.surface : undefined,
      paddingLeft: theme.spacing(1.5),
      paddingTop: theme.spacing(0),
      paddingBottom: theme.spacing(0),
      height: 30,
      "& p": {
        fontWeight: selected ? 700 : 400,
        fontSize: 16,
        color: theme.palette.text.primary,
      },
      "& svg": {
        fontSize: 20,
        color: theme.palette.text.primary,
      },
    }
  }
  return {
    ...baseButtonStyles(theme, selected, "click"),
    backgroundColor: menuOpen ? theme.palette.overlay.surface : baseBgColor,
    width: "110px",
    textTransform: "none",
    fontSize: 16,
    "&:hover": {
      backgroundColor: menuOpen
        ? theme.palette.overlay.surface
        : alpha(theme.palette.primary.main, 0.3),
    },
    "& > p": {
      fontWeight: 500,
      fontSize: 16,
      color: menuOpen ? baseColor : theme.palette.primary.main,
    },
    "& > svg": {
      fontWeight: 400,
      marginLeft: theme.spacing(0.5),
      color: menuOpen ? baseColor : theme.palette.primary.main,
      transition: "transform 0.2s ease-in-out",
      transform: menuOpen ? "rotateZ(45deg)" : undefined,
    },
  }
})

const HeaderAvatar = styled(Avatar)(({ theme }) => ({
  backgroundColor: theme.palette.overlay.surface,
  color: theme.palette.text.secondary,
  cursor: "pointer",
  fontSize: 14,
  width: 30,
  height: 30,
}))

const HeaderMenu = styled(Menu)(({ theme }) => ({
  ".MuiPaper-root": {
    backgroundColor: theme.palette.elevation.dp08,
    minWidth: 200,
    marginLeft: "12px",
  },
  a: {
    color: theme.palette.text.primary,
    textDecoration: "none",
  },

  svg: {
    color: theme.palette.grey[300],
  },
}))

const styledSideBarSubMenu: SxProps<RPThemeProps> = theme => ({
  width: "172px",
  padding: theme.spacing(0.5),
  backgroundColor: theme.palette.elevation.dp01,
  boxShadow: "0px 11px 23px 0px #0000004D",
  borderRadius: "4px",
  ml: 1,
  display: "flex",
  gap: 0.5,
  flexDirection: "column",
  "& a.MuiMenuItem-root": {
    borderRadius: "4px",
    padding: 0.5,
  },
  "& a em": {
    borderRadius: "4px",
    p: 0.5,
    display: "flex",
    alignItems: "center",
    backgroundColor: alpha(theme.palette.primary.main, 0.2),
  },
  "& em svg": {
    fill: theme.palette.primary.main,
  },
  "& a.MuiMenuItem-root:hover": {
    backgroundColor: theme.palette.overlay.surface,
  },
  "& a p": {
    marginLeft: "12px",
  },
})

const CollapsableSideBarDrawerStyled = styled(Drawer, {
  shouldForwardProp: prop => prop !== "open",
})(({ theme, open }) => ({
  "& .MuiDrawer-paper": {
    position: "relative",
    whiteSpace: "nowrap",
    width: SIDEBAR_WIDTH,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    boxSizing: "border-box",
    ...(!open && {
      overflowX: "hidden",
      transition: theme.transitions.create("width", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      width: COLLAPSED_SIDEBAR_WIDTH,
    }),
  },
}))

export {
  StyledSideBarLinkContained,
  HeaderAvatar,
  HeaderMenu,
  styledSideBarSubMenu,
  StyledSideBarMenu,
  CollapsableSideBarDrawerStyled,
}
